html, body, #app {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  z-index: 1;
  width: 100%;
  height: 100%;
  font-family: Arial, "Microsoft YaHei", "\5FAE\8F6F\96C5\9ED1", Verdana, sans-serif;
}
ul, li {
  padding: 0;
  margin: 0;
  list-style: none;
}
* > img {
  max-width: 100%;
  max-height: 100%;
}
button {
  position: relative;
  display: block;
  margin-left: auto;
  margin-right: auto;
  padding-left: 14px;
  padding-right: 14px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  font-size: 18px;
  text-align: center;
  text-decoration: none;
  line-height: 2.55555556;
  border-radius: 5px;
  -webkit-tap-highlight-color: transparent;
  overflow: hidden;
  color: #000000;
  background-color: #F8F8F8;
}
button::after {
    content: " ";
    width: 200%;
    height: 200%;
    position: absolute;
    top: 0;
    left: 0;
    border: 1px solid rgba(0, 0, 0, 0.2);
    -webkit-transform: scale(0.5);
    transform: scale(0.5);
    -webkit-transform-origin: 0 0;
    transform-origin: 0 0;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    border-radius: 10px;
}
.page-group {
  position: absolute;
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
}
.page {
  background-color: #FFF;
  overflow: hidden;
}
.page-current {
    z-index: 1;
}
.slide-enter, .slide-leave-to {
  -webkit-transform: translate(100%, 0);
  transform: translate(100%, 0);
}
.slide-enter-active, .slide-leave-active {
  -webkit-transition: all 0.5s cubic-bezier(0.55, 0, 0.1, 1);
  transition: all 0.5s cubic-bezier(0.55, 0, 0.1, 1);
}
.slide-enter-to, .slide-leave {
  -webkit-transform: translate(0, 0);
  transform: translate(0, 0);
}

